/* Estilos base para la lista de sugerencias */
.suggestion-list {
  width: 100%;
  list-style: none;
  display: flex;
  grid-gap: 1.25rem;
  gap: 1.25rem;
  margin-top: 9.5vh;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
  scrollbar-width: none; /* Para Firefox */
  color: white;
  background-color: #282c34;
}

/* Ocultar la scrollbar en navegadores WebKit */
.suggestion-list::-webkit-scrollbar {
  display: none;
}

/* Estilo para cada cuadro (sugerencia) */
.suggestion-list .suggestion {
  cursor: pointer;
  padding: 1.25rem;
  width: 222px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: center;       /* Centramos el contenido horizontalmente */
  justify-content: center;   /* Centramos verticalmente */
  border-radius: 0.75rem;
  background: #1a1a1a;        /* Fondo más oscuro */
  transition: transform 0.3s ease, box-shadow 0.3s ease, background-color 0.3s ease;
  /* Sombra en la parte superior y en la inferior con mayor intensidad */
  box-shadow: 0 -4px 8px rgba(0, 0, 0, 0.5), 0 4px 8px rgba(0, 0, 0, 0.5);
  scroll-snap-align: center;
}

/* Efecto hover: fondo aún más negro y sombras más intensas (por arriba y abajo) */
.suggestion-list .suggestion:hover {
  background: #000;           /* Fondo aún más oscuro */
  transform: scale(1.03);
  box-shadow: 0 -6px 12px rgba(0, 0, 0, 0.7), 0 6px 12px rgba(0, 0, 0, 0.7);
}

/* Ajuste de tipografía e íconos */
.suggestion-list .suggestion :where(.text, .icon) {
  font-weight: 600;
  text-align: center;
}

/* Estilos específicos para el ícono dentro de la sugerencia */
.suggestion-list .suggestion .icon {
  width: 42px;
  height: 42px;
  display: flex;
  font-size: 1.3rem;
  margin-top: 1rem;
  align-self: center;
  align-items: center;
  border-radius: 50%;
  justify-content: center;
  background: var(--primary-color);
  color: var(--text-color);
}

/* Adaptaciones responsivas para dispositivos móviles */
@media (max-width: 768px) {
  .suggestion-list {
    grid-gap: 1rem;
    gap: 1rem;
    margin-top: 6vh;
    padding: 0 1rem; /* Evita que los cuadros queden pegados a los bordes */
  }
  .suggestion-list .suggestion {
    width: 80%;
    padding: 1rem;
  }
}

